.App {
  
  text-align: center;
  align-items: center;
}

.App-logo {
  border-radius: 50%;
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}

.App-header {
  background-color: #28b5b5;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
  
}
body {

  align-items: center;
  
  background-color: #28b5b5;
  color: #d2e69c;
  
  font-family: Arial;
  text-align: center;
  
}

h1 {
  color: #d2e69c;
}

